    border: 2px solid #ccc;
    border-radius: 4px;
    outline: none;
    position: relative;
    cursor: pointer;
  }
  
  .selectall::before {
    content: '';
    position: absolute;
    /* size: a3; */
    top: 50%;
    left: 48%;
    transform: translate(-50%, -50%);
    width: 13px;
    height: 3px;
    background-color: #FFFFFF;
    transition: background-color 0.3s ease;
  }
  
  .selectall:checked::before {
    background-color: #FFFFFF; /* Change the background color when checked */
  }
  
  .selectall:checked + label:before {
    /*content: '\2023';  Unicode character for the checkmark symbol */
    font-size: 14px;
    color: #007bff;
    margin-left: 6px;
  }
  
